<template>
  
  <v-stepper v-model="currentStep">
    <camera-dialog @captureClose="onCaptureClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-snackbar v-model="snackbar" :color="snackbarColor">
      Member #{{currentStep}}, Similarity: {{member.similarity}}
      <v-btn dark flat @click.native="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :color="getStepColor(n)"
          :complete="isComplete(n)"
          :rules="[() => isError(n)]"
          :key="`${n}-step`"
          :step="n"
          edit-icon="$vuetify.icons.complete"
          error-icon="$vuetify.icons.info"
          editable
        >
          {{ getStepLabel(n)}}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items 
      v-touch="{
        left: () => swipe('Left'),
        right: () => swipe('Right'),
      }"
    >
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
        <v-layout align-center row justify-center>
         <v-flex xs6>
            <v-text-field label="Full Name"  v-model="member.fullName" v-if="member" :rules="[rules.required]"></v-text-field>
          </v-flex>
        </v-layout>
        <v-layout row fill-height>
          <v-flex xs6>
            <v-btn round :color="idBtnColor" class="headline" @click.native="showCameraDialog(true)">
              {{ $t(idBtnLabel) }}
              <v-icon right dark>touch_app</v-icon>
            </v-btn>
          </v-flex>
          <v-flex xs6>
            <v-btn round :color="faceBtnColor" class="headline" @click.native="showCameraDialog(false)">
              {{ $t(faceBtnLabel) }}
              <v-icon right dark>touch_app</v-icon>
            </v-btn>
          </v-flex>
        </v-layout>  
        <v-layout row fill-height>
          <v-flex xs12>
            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs6>
                  <img :src="member.idImgSrc" class="img-responsive" :width="imgWidth" @click="showCameraDialog(true)" style="cursor: pointer;"/>
                </v-flex>
                <v-flex xs6>
                  <img :src="member.faceImgSrc" class="img-responsive" :width="imgWidth" @click="showCameraDialog(false)" style="cursor: pointer;"/>
                </v-flex>
              </v-layout>
            </v-container>
          </v-flex>
        </v-layout>


      </v-stepper-content>
    </v-stepper-items>

  </v-stepper>
</template>



<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus'
import { Storage } from 'aws-amplify'
import aws_exports from '@/aws-exports'
import { mapState, mapGetters, mapActions } from 'vuex'
import { CallDialog } from '@/utils'
import store from '@/store'

export default {
  name: 'CheckIn',
  data () {
    return {
      snackbar: false,
      snackbarColor: null,
      rules: {
        required: value => !!value || 'Please set your full name first.',
      },
    }
  },
  created () {
    this.setCurrentStep(1)
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      // store.dispatch('checkIn/setCurrentStep', 1)
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('checkIn/loadMembers').catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },

  computed: {
    idBtnColor: {
      get() {
        if (this.member.idVerified) {
          return 'success'
        } else {
          if (this.member.idImgName) {
            return 'warning'  
          } else {
            return 'error'
          }
        }
      }
    },
    faceBtnColor: {
      get() {
        if (this.member.idMatches) {
          return 'success'
        } else {
          if (this.member.faceImgName) {
            return 'warning'  
          } else {
            return 'error'
          }
        }
      }
    },
    idBtnLabel: {
      get() {
        if (this.member.idVerified) {
          return 'checkIn.btn_id_verified'
        } else {
          if (this.member.idImgName) {
            return 'checkIn.btn_id_wait'  
          } else {
            return 'checkIn.btn_id_take'
          }
        }
      }
    }, 
    faceBtnLabel: {
      get() {
        if (this.member.idMatches) {
          return 'checkIn.btn_rekog_matches'
        } else {
          if (this.member.idVerified) {
            if (this.member.faceImgName) {
              return 'checkIn.btn_face_retake'  
            } else {
              return 'checkIn.btn_face_take'
            }
          } else {
            if (this.member.faceImgName) {
              return 'checkIn.btn_rekog_wait'  
            } else {
              return 'checkIn.btn_face_take'
            }
          }

        }
      }
    }, 
    imgWidth: {
      get() {
        if (this.$window.width > this.$window.height) {
          return '60%'
        } else {
          return '100%'
        }
      }
    }, 
    currentStep: {
      get() {
        return this.$store.state.checkIn.currentStep
      },
      set (val) {
        this.setCurrentStep(val)
      }
    },
    ...mapState({
      identityId: state => state.session.identityId,
      members: state => state.checkIn.members,
      member: state => state.checkIn.currentMember,
    }),
    ...mapGetters('checkIn', {
      steps: 'steps',
    })
  },
  methods: {
    getStepLabel(n) {
      if (n === this.currentStep) {
        if (!this.member.fullName) {
          return this.$t('checkIn.step_label_fullname_input')
        }

        if (!this.member.idImgName || !this.member.faceImgName) {
          return this.$t('checkIn.step_label_pictures_take')
        } else {
          if (this.member.idMatches) {
            return this.$t('checkIn.step_label_rekog_matches')
          } else {
            if (this.member.idVerified) {
              return this.$t('checkIn.step_label_face_take')
            } else {
              return this.$t('checkIn.step_label_rekog_wait')
            }
          }
        }
      } else {
        return '#' + parseInt(n)
      }
    },
    getStepColor(n) {
      if (n === this.currentStep) {
        return 'primary'
      } else {
        if (this.members[n - 1].idVerified) {
          if (this.members[n - 1].idMatches) {
            if (this.members[n - 1].fullName) {
              return 'success'
            }
          }
        }
      }
    },
    isError (n) {
      if (n === this.currentStep) {
        return true
      } else {
        if (this.members[n - 1].idVerified) {
          if (this.members[n - 1].idMatches) {
            if (this.members[n - 1].fullName) {
              return true
            }
          }
        }

        return false
      }
    }, 
    isComplete (n) {
      if (this.members[n - 1].idVerified && this.members[n - 1].idMatches) {
        return true
      } else {
        return false
      }
    }, 
    swipe (direction) {
      this.swipeDirection = direction
      if (direction === 'Left') {
        this.goNext()
      } else if (direction === 'Right') {
        this.goPrevious()
      }
    },
    prevStep (n) {
      if (n === 1) {
        this.setCurrentStep(this.steps)

      } else {
        this.setCurrentStep(n - 1)
      }
    },    
    nextStep (n) {
      if (n === this.steps) {
        this.setCurrentStep(1)
      } else {
        this.setCurrentStep(n + 1)
      }
    },
    goPrevious () {
      this.prevStep(this.currentStep)
    },
    goNext () {
      this.nextStep(this.currentStep)
    },
    showCameraDialog(isId) {
      if (this.member.idMatches) {
        const rtn = window.confirm('ID already recognized. Are you sure you want to re-take the picture?')
        if (!rtn) {
          return
        }
      }

      if (isId) {
        if (this.member.idVerified) {
          const rtn = window.confirm('ID already verified by the host. Re-taking it will need the host to verify it again. Are you sure?')
          if (!rtn) {
            return
          }
        }
      }

      EventBus.$emit('CameraDialog', { showDialog: true, isId });
      // this.isId = isId;
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onCaptureClose(val) {
      if (val.base64Img) {
        if (val.isId) {
          await this.putImage({img: val.base64Img, isId: true})
        } else {
          await this.putImage({img: val.base64Img, isId: false})
        }

        if (this.member.idVerified && this.member.idImgKey && this.member.faceImgKey) {
          
          await this.rekogId()

          console.log(`similarity: ${this.member.similarity}\nmessageCode: ${this.member.messageCode}`)

          this.snackbar = true
          if (this.member.similarity) {
            this.snackbarColor = 'success'
          } else {
            this.snackbarColor = 'warning'
          }
          
        }

        if (!this.member.fullName) {
          this.member.fullName = null
        }

        const error = await this.saveMember(val.isId).catch(e => {
          console.error('saveMember', e)
          return e
        })

        if (error) {
          CallDialog(error)
          return
        }
        
      }
    },
    ...mapActions({
      loadMembers: 'checkIn/loadMembers',
      setCurrentStep: 'checkIn/setCurrentStep',
      putImage: 'checkIn/putImage',
      rekogId: 'checkIn/rekogId',
      saveMember: 'checkIn/saveMember',
    })
  },
  components: {
    cameraDialog: CameraDialog,
  }
}
</script>